
.hero-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .hero-image {
    width: 300px;
    height: 300px;
    object-fit: cover;
    object-position: 43% 30%;
    border-radius: 50%;
    border: 5px solid #545583;
    z-index: 10;
    margin-top: 100px;
  }
  
  .title {
    color: #FFF;
    text-align: center;
    font-family: Outfit;
    font-size: 50px;
    font-style: normal;
    font-weight: 700;
    line-height: 66px;
    margin-top: 10px;
  }
  
  .description {
    color: #545583;
    text-align: center;
    font-family: Outfit;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: 20px;
    margin-top: -60px;
  }
  
  .button-container {
    margin-top: 20px; 
  }
  
  .say-hi-button {
    border: 5px solid #DC4280;
    background: #17181C;
    color: #DC4280;
    text-align: center;
    font-family: Outfit;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: 20px; 
    padding: 10px 20px;
    cursor: pointer;
    display: flex;
    align-items: center;
    margin-top: -10px
  }
  
  .say-hi-button svg {
    margin-right: 10px;
  }
  